.typewriter-console {
    display: flex;
    justify-content: center;
  }
  
  .terminal {
    background-color: #000000;
    color: #8abeb7; /* Light green color */
    padding: 20px;
    width: 97%;
    border-radius: 5px;
    border: 1px solid #333;
    box-shadow: 0 0 10px rgba(0, 139, 139, 0.7);
    display: flex;
    flex-direction: column;
  }
  
  .terminal-header {
    font-weight: bold;
    padding-bottom: 10px;
  }
  
  