.App {
  font-family: 'Courier New', Courier, monospace;
  color: #c0c0c0;
  background-color: #2e3436; /* Dark gray background */
  margin: 0;
  padding: 0;
}

section {
  padding: 20px;
  margin: 20px;
  border: 1px solid #333;
  border-radius: 5px;
  background-color: #1d1f21; /* Slightly lighter dark gray */
}

h1, h2, h3 {
  color: #8abeb7; /* Light green color */
}

a {
  color: #8abeb7;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.profile-pic {
  border: 3px solid #8abeb7;
}